import React from 'react'
import Layout from '../components/layout'

const Footer = ({ siteTitle }) => (
  <Layout>
    <div>
      <p style={{color: 'red', fontSize:'2rem', textShadow:'2px 2px 2px yellow'}}>Wanna Know Me ?</p>
      <p>
        I'm a passionate Frontend Developer, who loves to work with JavaScript and ReactJS.
      </p>
      <p>
        I'm currently exploring under the hood functionalities of JavaScript.
      </p>
      <p>
        I'm under metamorphosis to become a Full-Stack Dev.
      </p>
      <div>
        <p style={{color: 'red', fontSize:'2rem', textShadow:'2px 2px 2px yellow'}}>Why I'm Special ?</p>
        <p>
          <ul>
            <li><span className="underline">Unending enthusiasm to learn</span> new stuff.</li>
            <li><span className="underline">Loves to teach</span> the stuff I learn.</li>
            <li><span className="underline">Loves to lead</span> people and help them in reaching their goals along with making my company proud.</li>
          </ul>
        </p>
      </div>
      
      <p></p>
    </div>
  </Layout>
)

export default Footer
